	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Thus Spake The Poodle","date":"19 Sep 2010","slug":"/2010-09-19-thus-spake-the-poodle/","tags":"Y2010"},"html":"<p><a href=\"http://www.papascott.de/archives/2010/09/15/the-poodle-is-watching/\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/09/Der_Pudel-224x300.jpg\" alt=\"The Poodle\" align=\"right\" /></a>I promised to explain <a href=\"http://www.papascott.de/archives/2010/09/15/the-poodle-is-watching/\">the poodle</a>.</p>\n<p>Last weekend the 5th annual Whiney Expat Blogger Meet-Up was held in Hamburg. Tradition holds that once a date and location are decided (by democratic vote in a process somewhat similar in length and complexity to choosing a US presidential candidate), the local bloggers, in this case <a href=\"http://lettershometoyou.wordpress.com/\">Ian</a>, <a href=\"http://noapathyallowed.com/\">No Apathy Allowed</a>, <a href=\"http://transkitten.com/\">Transkitten</a> and I, plan the weekend.  Over the years a successful formula for the weekend has evolved: a side-trip on Friday, dinner Friday evening, a walking tour Saturday morning followed by lunch, disco nap in the afternoon, dinner and (gay) bar hopping Saturday night, and brunch on Sunday.</p>\n<p>This year for walking tour we called in professional help, an excellent decision since even we natives learned new things about the most beautiful city in the world (as the city is modestly referred to by its residents). Something new for us all was the poodle. The shipping magnate Carl Laeisz placed the poodle atop his company's headquarters, the <a href=\"http://www.hamburgwiki.de/wiki/Laeiszhof\">Laeiszhof</a> on the Nikolaifleet in honor of his wife, whose nickname was \"poodle\". He even gave all his ships names beginning with \"P\", a tradition <a href=\"http://www.laeisz.de/en/index.html\">the company</a> continues to this day.</p>\n<p>The women in the group all agreed that they wished their men would similarly honor them, and the poodle became the official mascot of the weekend. One of them even took to giving commands as the poodle in the third person, as in \"The poodle demands your attention\", \"The poodle is not amused\", until finally, at the end of the evening, \"The poodle has left the building\".</p>\n<p>Alas, for me the honor would be counter-productive, as I would have to erect and statue of a mouse atop our restaurants, and as the move <a href=\"http://www.hamburgwiki.de/wiki/Laeiszhof\">Ratatouille</a> shows, rodents and gastronomy do not mix.</p>"},"next":{"fields":{"title":"The Poodle Is Watching","date":"15 Sep 2010","slug":"/2010-09-15-the-poodle-is-watching/"}},"previous":{"fields":{"title":"Blue skies, Minneapolis skyline, Jim Thome","date":"22 Sep 2010","slug":"/2010-09-22-blue-skies-minneapolis-skyline-jim-thome/"}}},{"node":{"fields":{"title":"The Poodle Is Watching","date":"15 Sep 2010","slug":"/2010-09-15-the-poodle-is-watching/","tags":"Y2010"},"html":"<p><a href=\"http://www.hamburgwiki.de/wiki/Bild:Der_Pudel.jpg\"><img style=\"margin-left:auto;margin-right:auto\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2010/09/Der_Pudel.jpg\" alt=\"Der_Pudel.jpg\" border=\"0\" width=\"449\" height=\"599\" /></a></p>\n<p>Sculpture atop the <a href=\"http://www.hamburgwiki.de/wiki/Laeiszhof\">Laeiszhof in Hamburg</a>. Explanation will follow. (Source: <a href=\"http://www.hamburgwiki.de/wiki/Bild:Der_Pudel.jpg\">StadtWiki Hamburg</a>  &copy; H. Bergmann)</p>"},"next":{"fields":{"title":"It Was Twenty Years Ago Today","date":"01 Sep 2010","slug":"/2010-09-01-it-was-twenty-years-ago-today/"}},"previous":{"fields":{"title":"Thus Spake The Poodle","date":"19 Sep 2010","slug":"/2010-09-19-thus-spake-the-poodle/"}}},{"node":{"fields":{"title":"It Was Twenty Years Ago Today","date":"01 Sep 2010","slug":"/2010-09-01-it-was-twenty-years-ago-today/","tags":"Y2010"},"html":"<p>On this day in 1990 we arrived in Germany, with all the suitcases we could carry plus 3 cats, the rest of our belongings in a half-container on a slow boat, for what we thought would be a 3 to 5 year stint. Saddam had just invaded Kuwait, Germany had just won the World Cup, and we still didn't know who had killed Laura Palmer. Germany was still in two pieces, but the marriage papers had just been signed the day before and the party would be in a month.</p>\n<p>Coming to Germany was an impulse decision, and it was better to be lucky than good. We were in the right place at the right time, reunified Germany was where the action was. Change was happening before our eyes and history was being made at our feet. We had left the US during that decade's banking crisis and the beginning of a war. The US was slow and broken down; Germany was <em>exciting</em>.</p>\n<p>We landed first in a run-down top-floor apartment in Kiel with a 4 month lease. We thought we'd be heading to Berlin, but it became clear we'd be staying in Hamburg. Housing was tight, the whole country was on the move, and 50 people would show up for an apartment showing. I think in the end someone pulled a string for us, and we moved into a 3 room apartment in Barmbek just as the slow boat with our belongings pulled up.</p>\n<p>My introduction to German life was total immersion. There was no internet, and I knew no other expats. I was naive and optimistic enough to believe that my American degree and determination would be enough to get a job. In the end it worked, but it took 3 years.</p>\n<p>I wonder sometimes if today, we were 20 years younger and the same opportunity came up again, we would make the same decision. Times have changed. I somehow doubt it. </p>"},"next":{"fields":{"title":"Twenty-Five Plus","date":"15 Aug 2010","slug":"/2010-08-15-twenty-five-plus/"}},"previous":{"fields":{"title":"The Poodle Is Watching","date":"15 Sep 2010","slug":"/2010-09-15-the-poodle-is-watching/"}}}],"pathPrefix":"","first":false,"last":false,"index":40,"pageCount":1101}}

